@include font(Room Thin, room-thin);
@mixin fl {
	font-family: "Room Thin", Arial, sans-serif; }
@include font(Room Bold, room-bold);
@mixin fb {
	font-family: "Room Bold", Arial, sans-serif; }
@include font(Clear Sans Regular, clearsans-regular);
@mixin fr {
	font-family: "Clear Sans Regular", Arial, sans-serif; }

body {
	min-width: 320px;
	min-height: 480px;
	@include fl;
	font-size: 14px;
	line-height: 1.3;
	color: #000;
	transition: background .7s;
	transition-delay: .2s; }
button {
	background: none; }
a {
	text-decoration: none; }

*::selection {
	background: rgba(#fff,.2);
	color: #FF7D71; }

.wrapper {
	position: relative;
	height: 100%; }

.center {
