		padding: 0 10px;
		height: 62px;
		background: #000;
		background-color: #000;
		font-size: 0;
		.lt-ie9 & {
			display: block;
			font-size: 14px;
			left: auto;
			right: -56px;
			border: 1px solid #000;
			background-color: #000;
			background: #000 url(../img/nav.png) no-repeat 50% 50%; }
		@include r(767) {
			top: 140px;
			width: 46px;
			height: 50px; }
		&:before,
		&:after {
			content: "";
			position: absolute;
			width: 20px;
			left: 50%;
			margin-left: -10px;
			transition: all .4s; }
		&:before {
			top: 25px;
			height: 2px;
			background: #FFF;
			.lt-ie9 & {
